// Reset ustawień przeglądarki
@import '../sass/partials/base_reset';

// Topografia
@import '../sass/partials/base_typography';

// ------------------> STYLE SEKCJI (LAYOUT) <------------------

// Główne style nagłówka
@import '../sass/partials/layout_header';

// Pomocnicze style dla poszczególnych elementów layoutu tworzymy w ten sposób: @import './sass/partials/layout_header_01';
