// Główne style galerii
@import '../sass/partials/layout_gallery';

// Główne style stopki
@import '../sass/partials/layout_footer';
@import '../sass/partials/layout_footer_modal';

// Główne style okna modalnego
@import '../sass/partials/layout_modal';

